@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;900&family=Raleway:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  color: white;
}

li {
  list-style: none;
}

.hero {
  height: 100vh;
  width: 100%;
  background-color: #000000;
  color: #fff;
  position: relative;
  background: #0000008a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 15%;
}

.hero:before {
  content: "";
  position: fixed;
  background: #040404 url(/swaraj344.github.io/static/media/man2.c5ccf11d.jpg) top right no-repeat;
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: -1;
}
.hero h1 {
  /* background-color: red; */
  margin: 0;
  font-size: 4.5rem;
  font-weight: 700;
  line-height: 56px;
  font-family: sans-serif;
}

.hero p {
  /* background-color: yellow; */
  margin-top: 14px;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
}
.hero ul {
  /* background-color: violet; */
  display: flex;
  margin-top: 24px;
}
.hero ul li a i {
  text-decoration: none;
  color: white;
  font-size: 24px;
  margin-right: 18px;
  transition: 0.3s;
}
.hero ul li a i:hover {
  color: blue;
  transform: translateY(-2px);
}

#header {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;

  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  transition: all 1s;
}

.nav-menu * {
  text-decoration: none;
}

.nav-menu ul {
  list-style: none;
}

.nav-menu > ul > li {
  display: flex;
  align-items: center;
  position: relative;
  white-space: nowrap;

  height: 30px;
  margin: 20px 0;
  padding: 28px 0;
}
.nav-menu > ul > li > a {
  font-weight: bold;
  color: white;
  width: 55px;
  background-color: rgba(0, 0, 0, 0.233);
  border-radius: 999px;
  display: flex;
  align-items: center;
  transition: 0.5s;
}
.nav-menu > ul > li > a > i {
  font-size: 20px;
  padding: 18px 8px 18px 18px;
}

.nav-menu > ul > li > a > span {
  display: none;
  padding-right: 15px;
}
.nav-menu > ul > li > a:hover span {
  display: block;
}
.nav-menu > ul > li > a:hover {
  background-color: white;
  color: black;
  width: 100%;
}

